.product-attr {
  margin: 0.625rem auto;
  clear: both;
  min-height: 3.125rem;
  background-color: #fff;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
}
.product-attr ul {
  width: 100%;
  clear: both;
  margin: 0;
  padding: 0;
  display: block;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 0.625rem;
}
.product-attr ul li {
  margin-left: 0;
  background-color: #fff;
  overflow: hidden;
  line-height: 1.125rem;
  display: block;
}
.product-attr ul .name {
  font-size: 0.875rem;
  height: 1.125rem;
  font-weight: 400;
  padding-right: 0.625rem;
  text-align: right;
  width: 6.875rem;
  display: inline-block;
}
.product-attr ul .value {
  display: inline-block;
  font-size: 0.875rem;
  height: 1.125rem;
  font-weight: 300;
}
